body {
  margin: 20px;
  background-color: #222831;
  color: #a9cfd1;
  font-size: 0.6rem;
}

h1 {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  text-align: center;
  color: #b1f9fe;
}

h3 {
  border: 1px solid;
  border-radius: 10px;
  text-align: center;
  padding: 10px;
}

.greyed-text {
  color: rgb(198, 198, 198);
  text-align: center;
  opacity: 0.4;
  font-size: 0.8rem;
  font-style: italic;
}

.logo-img {
  width: 200px;
  /* margin: 5px; */
}

.coin-title {
  text-align: center;
  font-size: 2rem;
  margin: 20px 0 20px 0;
  color: #b1f9fe;
  border: 2px solid #638b8e;
  border-radius: 10px;
}

.box {
  border: 3px solid #638b8e;
  border-radius: 20px;
  padding: 25px;
}

.box-yellow {
  border: 3px solid #000;
  padding: 25px;
  background-color: yellow;
}

img {
  width: 30px;
  margin: 5px;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
}

footer {
  text-align: center;
  margin-top: 20px;
}

.divider {
  margin-top: 30px;
  margin-bottom: 20px;
  border-bottom: 1px solid #638b8e;
}
